<template>
<div class="subpage--main">
  <Card>
    <p slot="title">分析挖掘流程列表</p>
    <EasyTable
      class="table"
      :table="table"
      border
      stripe
    />
  </Card>
  <Modal
    v-model="jobDetailModal.isShown"
    width="800"
    title="运行日志"
    footer-hide
  >
    <p v-html="jobDetailModal.content"></p>
  </Modal>
</div>
</template>

<script>
import {
  Button,
  Timeline,
  TimelineItem,
  Progress
} from 'iview'
import EasyTable, { createTable } from '@/components/EasyTable'
import {
  dateFormat,
} from '@/utils/date'

const STATUS_MAP = {
  error: '出错',
  runing: '正在运行',
  stop: '停止',
  success: '已完成'
}

export default {
  components: {
    EasyTable,
  },
  data() {
    return {
      jobDetailModal: {
        isShown: false,
        content: ''
      },
      OPTIONS_MAP: {
        [STATUS_MAP.runing]: ['停止'],
        [STATUS_MAP.error]: ['启动', '删除'],
        [STATUS_MAP.stop]: ['启动', '删除'],
        [STATUS_MAP.success]: ['启动', '删除']
      },
      table: createTable({
        loadData: async ({
          PAGE_SIZE,
          current,
        }) => {
          const {
            list: items,
            total
          } = await this.$api.kettle.listJobs.send({
            pageSize: PAGE_SIZE,
            pageNum: current
          })
          return {
            items,
            total
          }
        },
        COLUMNS: [
          {
            type: 'expand',
            width: 50,
            render: (h, { row }) => (
              <Timeline>
                {row.componentStatus.map(vo => (
                  <TimelineItem>
                    <p class="time">{vo.execDate}</p>
                    <p class="content">
                      {vo.componentName} ({vo.status})
                    </p>
                  </TimelineItem>
                ))}
              </Timeline>
            )
          },
          {
            title: '序号',
            type: 'index',
            width: 80,
            align: 'center'
          },
          {
            title: '作业名称',
            key: 'jName',
            align: 'center',
            render(h, { row }) {
              return <span>{row.jName.replace(/\.\w+$/, '')}</span>
            }
          },
          {
            title: '作业状态',
            key: 'status',
            align: 'center'
          },
          {
            title: '业务类型',
            key: 'businessType',
            align: 'center'
          },
          {
            title: '执行方式',
            key: 'excuteWay',
            align: 'center'
          },
          {
            title: '执行时间',
            key: 'createDate',
            align: 'center',
            render: (h, { row }) => <span>{dateFormat(row.createDate)}</span>
          },
          {
            title: '执行进度',
            key: 'progress',
            align: 'center',
            render(h, { row }) {
              let status
              switch (row.status) {
                case STATUS_MAP.error:
                  status = 'wrong'
                  break
                case STATUS_MAP.success:
                  status = 'success'
                  break
                default:
                  status = 'active'
              }
              return <Progress percent={row.progress} status={status} />
            }
          },
          {
            title: '作业操作',
            align: 'center',
            render: (h, { row }) => {
              const curOption = this.OPTIONS_MAP[row.status]
              return (
                <div>
                  {
                    curOption.indexOf('启动') !== -1 ? (
                      <Button
                        type="success"
                        size="small"
                        style="marginRight: 5px"
                        onClick={() => this.startJob(row)}
                      >启动</Button>
                    ) : null
                  }
                  {
                    curOption.indexOf('停止') !== -1 ? (
                      <Button
                        type="primary"
                        size="small"
                        style="marginRight: 5px"
                        onClick={() => this.stopJob(row)}
                      >停止</Button>
                    ) : null
                  }
                  {
                    curOption.indexOf('删除') !== -1 ? (
                      <Button
                        type="error"
                        size="small"
                        onClick={() => this.deleteJob(row)}
                      >删除</Button>
                    ) : null
                  }
                </div>
              )
            }
          },
          {
            title: '运行日志',
            width: 85,
            align: 'center',
            render: (h, params) => {
              return (
                <Button
                  type="primary"
                  size="small"
                  onClick={() => {
                    this.jobDetailModal = {
                      isShown: true,
                      content: params.row.jLog
                    }
                  }}
                >详情</Button>
              )
            }
          }
        ]
      })
    }
  },
  created() {
    this.table.apply()
  },
  methods: {
    stopJob({ jId }) {
      this.$Modal.confirm({
        title: '系统提示',
        content: '您确定要停止此任务？',
        onOk: async () => {
          try {
            await this.$api.kettle.stopJob.send({ jId })
            this.$Message.success('成功停止任务！')
          } finally {
            this.table.refresh()
          }
        }
      })
    },
    async startJob({ jId }) {
      try {
        await this.$api.kettle.startJob.send({ jId })
        this.$Message.success('成功启动！')
      } finally {
        this.table.refresh()
      }
    },
    deleteJob({ jId }) {
      this.$Modal.confirm({
        title: '系统提示',
        content: '您确定要删除此任务？',
        onOk: async () => {
          try {
            await this.$api.kettle.deleteJob.send({ jId })
            this.$Message.success('成功删除任务！')
          } finally {
            this.table.refresh()
          }
        }
      })
    },
  },
};
</script>
